import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../helper/function";
import { MenuService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import SearchComponent from "../../core/SearchComponent";

export default function ModifyMenuComponent(props: any) {
    const [dataSearch, setDataSearch] = useState([]);
    const [parentKey, setParentKey] = useState(null);
    const [parentName, setParentName] = useState(null);

    const [title, setTitle] = useState(null);
    const [route, setRoute] = useState(null);
    const { isLoading, setIsLoading, keyData, setKeyData, visible, setVisible, loadData } = props;

    const loadDataDetail = useCallback(() => {
        if (keyData !== undefined) {
            setIsLoading(true);
            MenuService.GetDetail(keyData)
                .then((res: any) => {
                    if (res.isSuccess) {
                        setTitle(res.data.title);
                        setRoute(res.data.route);
                        if (res.data.parent !== null) {
                            setParentKey(res.data.parent.key);
                            setParentName(res.data.parent.title + (" - ") as string | undefined + res.data.parent.route);
                        }
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [keyData, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail]);

    function handleOnClose() {
        setVisible(false);
        if (keyData !== undefined) {
            setKeyData(undefined);
        }
        setTitle(null);
        setRoute(null);
        setParentKey(null);
        setParentName(null);
    }

    function handleSearch(value: string) {
        setIsLoading(true);
        MenuService.Query(value)
            .then((res: any) => {
                if (res.isSuccess) {
                    setDataSearch(res.data);
                }
            })
            .finally(() => setIsLoading(false));
    }

    function handleSubmitForm() {
        var body = {
            title: title,
            route: route,
            parentKey: parentKey === undefined || parentKey === '' || parentKey === null ? null : parentKey
        };

        setIsLoading(true);
        if (keyData === undefined) {
            add(body);
        } else {
            edit(body);
        }
    }

    function add(body: any) {
        MenuService.Add(body)
            .then((res: any) => {
                if (res.isSuccess) {
                    Fn.PrintMsgSuccessV2(res);
                    loadData();
                }
                else { setIsLoading(false); }
            })
            .finally(() => setIsLoading(false));
    }

    function edit(body: any) {
        MenuService.GetDetail(keyData)
            .then((res: any) => {
                if (res.isSuccess) {
                    body = Fn.GetTimestampV2(res, body);
                    MenuService.Edit(keyData, body)
                        .then((res: any) => {
                            if (res.isSuccess) {
                                Fn.PrintMsgSuccessV2(res);
                                loadData();
                            }
                            else { setIsLoading(false); }
                        })
                        .finally(() => setIsLoading(false));
                }
            })
    }

    function handleDelete(key: string) {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa không?"
        })
        .then((rs) => {
            if(rs.isConfirmed) {
                DoDelete();
            }
        })
    }

    function DoDelete() {
        MenuService.GetDetail(keyData)
        .then((res: any) => {
            if (res.isSuccess) {
                MenuService.Delete(keyData, Fn.GetTimestampV2(res))
                    .then((res: any) => {
                        if (res.isSuccess) {
                            Fn.PrintMsgSuccessV2(res);
                            loadData();
                            handleOnClose();
                        }
                        else { setIsLoading(false); }
                    })
                    .finally(() => setIsLoading(false));
            }
        })
    }

    return (
        <Drawer
            open={visible}
            title={keyData === undefined ? "Thêm mới menu" : "Chỉnh sửa menu"}
            onClose={handleOnClose}>
            <Form
                layout="vertical">
                <MyFormItem.Input
                    label="Title"
                    value={title}
                    setValue={setTitle} />

                <MyFormItem.Input
                    label="Route"
                    value={route}
                    setValue={setRoute} />

                <SearchComponent
                    label="Parent"
                    onSearch={handleSearch}
                    dataSearch={dataSearch}
                    resultKey={parentKey}
                    resultName={parentName}
                    setResultKey={setParentKey}
                    setResultName={setParentName}
                    isLoading={isLoading} />
                    
                <MyButton
                    value={keyData === undefined ? "Thêm mới" : "Chỉnh sửa"}
                    type="success"
                    isLoading={isLoading}
                    onClick={handleSubmitForm} />

                <MyButton
                    value="Xóa"
                    type="danger"
                    isLoading={isLoading}
                    onClick={handleDelete} />
            </Form>
        </Drawer>
    );
}