import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { MenuService } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";
import ModifyMenuComponent from "./ModifyComponent";

export default function ManageMenuIndexComponent() {
    const column: Array<TableColumn> = [
        new TableColumn({ title: "STT", key: "stt", render: (data: any, row: any, index: number) => Fn.RenderSTT(data, row, index, pageInfo) }),
        new TableColumn({
            title: "Title", key: "title", render: (value: string, row: any) =>
                <MyButton type="link" value={value} onClick={() => {
                    handleDetail(row.key);
                }} />
        }),
        new TableColumn({ title: "Route", key: "route" }),
        new TableColumn({
            title: "Parent", key: "parent", render: (value: any) =>
                <span>{value === null ? '*** Root ***' : value.title}</span>
        }),
        new TableColumn({
            title: "Ngày tạo", key: "createdAt", render: (value: any) =>
                <UIFormatDateTimeVNComponent value={value} />
        }),
        new TableColumn({
            title: "Ngày cập nhật cuối", key: "updatedAt", render: (value: any) =>
                <UIFormatDateTimeVNComponent value={value} />
        })
    ];

    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);

    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        MenuService.GetList(paginationRequest)
            .then((res: any) => {
                if (res.isSuccess) {
                    setPageInfo(new PageInfoModel(res));
                    setData(res.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false));
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickAdd() {
        setVisible(true);
    }

    function handleDetail(key: string) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    function RenderButtonList() {
        return (<MyButton
            type="primary"
            value="Thêm mới"
            onClick={handleClickAdd} />
        );
    }

    return (
        <MyBox
            title="Quản lý menu"
            isLoading={isLoading}
            buttonList={RenderButtonList}>

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData}
                orderBy={orderBy}
                setOrderBy={setOrderBy} />

            <ModifyMenuComponent
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ModifyMenuComponent
                keyData={keyEdit}
                setKeyData={setKeyEdit}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}